.dialogs{
    display: grid;
    grid-template-columns: 2fr 10fr;
}
.dialogItems{
    padding: 10px;
}
.dialogItems .active{
    color: gold;
}
.dialog{
    clear: both;margin-bottom:10px;
}
.dialog a{
    display:flex;align-items:center;
}
.messages{
    padding: 20px;
    color: white;
    background-color: #f5f5f5;
}
.message{
    clear:both;
    color: #333;
    width:100%;
    margin-bottom:10px;
}
.message:after{
    content:'';
    clear:both;
    display:block;
}
.message>div{width:100%}
.message .owner{
    float:right;
}
.message_content{background:#fff;padding:10px;border-radius:4px;min-width:50%;width:calc(100% - 120px);display:inline-block}
.message .owner{display:flex;justify-content:flex-end}
.message .owner img{order:2;margin-left:10px}
.message .owner div{order: 1}